function adminUpdateModuleStatus(moduleID, status) {
    const isConfirmed = confirm(' ,    ?');

    if (!isConfirmed) {
        return; //   ,  
    }

    if(status === 'ACTIVE') {
        status = 'INACTIVE'
    }else{
        status = 'ACTIVE'
    }

    //    
    fetch(`/admin/modules/updateStatus/${moduleID}/${status}`, {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
            'X-CSRF-TOKEN': document.querySelector('meta[name="_csrf"]')?.content || '' //  Spring Security CSRF
        }
    })
        .then(response => {
            if (!response.ok) {
                return response.json().then(errorData => {
                    throw new Error(errorData.message);
                });
            }
            return response.json();
        })
        .then(data => {
            //  
            showAlert(data.message, 'success');

            // :       DOM
            location.reload(); //  
            // : document.getElementById(`module-${id}`).remove();
        })
        .catch(error => {
            //  
            showAlert(`: ${error.message}`, 'error');
            console.error('   :', error);
        });
}

window.adminUpdateModuleStatus = adminUpdateModuleStatus;
